@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
	--grey-accent: rgb(198, 198, 198);
	--grey-light: rgb(232, 232, 232);
	--font-serif: "Lora", serif;
	--font-sans-serif: "Rubik", sans-serif;
	font-size: 14px;
}

body {
	font-family: "Rubik", sans-serif;
	overflow-y: scroll;
}

*,
*::before,
*::after,
h1,
h2,
h3,
p {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

a,
a:visited {
	text-decoration: none;
	color: black;
}

ul,
li {
	text-decoration: none;
	list-style: none;
}

button {
	cursor: pointer;
}

/* --- SHARED CLASSES --- */

.serif {
	font-family: var(--font-serif);
}

.fs-12rem {
	font-size: 1.2rem;
}

.fs-15rem {
	font-size: 1.5rem;
}

.fl-row-cont {
	display: flex;
}

.fl-col-cont {
	display: flex;
	flex-direction: column;
}

.fl-centered {
	justify-content: center;
	align-items: center;
}

.fl-align-center {
	align-items: center;
}

.fl-align-end {
	align-self: flex-end;
}

.fl-align-start {
	align-self: flex-start;
}

.btn-transparent {
	background: transparent;
	border: 0;
}

/* --- INDIVIDUAL --- */

.shared-layout {
	justify-content: space-between;
	min-height: 100vh;
}

#banner {
	background-color: var(--grey-accent);
	padding: 7px;
}

#header {
	background-image: url(../assets/header.jpg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 40px 0;
}

.header__main {
	color: rgb(255, 255, 255);
	font-family: "Lora", serif;
	text-transform: uppercase;
	font-weight: 700;
	font-style: italic;
	font-size: 3rem;
	text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.54);
}

.header__p {
	color: rgb(255, 255, 255);
	font-family: "Lora", serif;
	font-weight: 600;
	font-style: italic;
	text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.54);
}

#navbar {
	padding: 15px 5vw 15px 3vw;
	justify-content: space-between;

	-webkit-box-shadow: 0px 23px 17px -16px rgba(0, 0, 0, 0.13);
	box-shadow: 0px 23px 17px -16px rgba(0, 0, 0, 0.13);
}

.logo {
	font-family: "Lora", serif;
	letter-spacing: -2px;
	font-weight: 700;
}

.navbar__link {
	gap: 30px;
}

.navbar__link h2,
.logo__outer,
.promo__browseBtn {
	transition: all 200ms ease-in-out;
}

.navbar__link h2:hover,
.logo__outer:hover,
.promo__browseBtn:hover {
	transform: translateY(-3px);
}

.navbar__cart-qty {
	text-decoration: underline;
}

.footer {
	padding: 2vw 5vw;
	justify-content: space-evenly;
	gap: 2vw;

	background-color: var(--grey-light);
}

.footer__section {
	gap: 20px;
}

.footer__input {
	padding: 3px;
}

.footer__btn {
	background-color: black;
	font-weight: 700;
	color: white;
	border: 0;
	min-height: 20px;
	padding: 5px;

	text-transform: capitalize;
}

.footer__links {
	gap: 10px;
}

.footer__links > li {
	cursor: pointer;
}

.promo1 {
	height: 500px;

	background-image: url(../assets/promo1.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0% 50%;
}

.promo__padding,
.global__padding {
	padding: 20px;
}

.promo__browseBtn {
	border: 0;
	cursor: pointer;
	padding: 20px 40px;
	background-color: black;
	border-radius: 5px;
	color: white;
	font-size: 1.5rem;
	text-transform: uppercase;
	font-weight: 600;
}

.promo1__heading {
	font-size: 3rem;
}

.promo1__heading h1 {
	font-weight: 900;
	letter-spacing: 3px;
	color: rgb(0, 0, 0);
	text-shadow: 0px 0px 6px rgb(83, 164, 61);
}

.promo1__subtext {
	margin: 0 10px 10px;
	gap: 5px;
}

.promo2 {
	height: 500px;
	color: #ffffff;
	background: #f7c1c1;
	gap: 10px;
}

.promo2__heading {
	font-size: 3rem;
	text-shadow: 4px 3px 0px #7a7a7a;
}

.promo2__subheading {
	width: 100%;
	height: 50px;
	background-color: black;
	color: white;
}

.promo2__subtext {
	text-shadow: 2px 2px 0px #7a7a7a;
}

.promo2__browseBtn {
	border: 0;
	cursor: pointer;
	padding: 20px 40px;
	background-color: white;
	border-radius: 5px;
	color: black;
	font-size: 1.5rem;
	text-transform: uppercase;
	font-weight: 600;
}

.error-page {
	gap: 10px;
	min-height: 500px;
}

.products__categories {
	gap: 10px;
}

.products__category-btns {
	gap: 10px;
}

.products__category-btn {
	border: 2px solid black;
	border-radius: 10px;
	width: fit-content;
	padding: 5px 10px;
	cursor: pointer;
}

.products__category-btn--active {
	border: 2px solid black;
	border-radius: 10px;
	width: fit-content;
	padding: 5px 10px;
	background-color: black;
	color: white;
}

.productCategories {
	padding: 20px 10vw;
	gap: 30px;
}

.productCategories__cards {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	row-gap: 20px;
	column-gap: 20px;
}

.productCards__card {
	padding: 20px;
	gap: 5px;

	cursor: pointer;

	-webkit-box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.07);
	box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.07);

	border-radius: 7px;

	transition: all 200ms ease-in-out;
}

.productCards__card:hover {
	transform: translateY(-5px);
	-webkit-box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.13);
	box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.13);
}

.productCards__photo {
	aspect-ratio: 1/1;
	width: 200px;
	color: red;
	border-radius: inherit;

	-webkit-box-shadow: inset 0px 0px 14px 0px rgba(0, 0, 0, 0.07);
	box-shadow: inset 0px 0px 14px 0px rgba(0, 0, 0, 0.07);
}

.productCards__name {
	font-family: "Lora", serif;
	font-weight: 700;
	font-size: 1.2rem;
}

.productCards__price {
	margin-left: auto;
	font-weight: 700;
	font-family: var(--font-serif);
}

.productInfo__image {
	aspect-ratio: 1/1;
	width: 400px;
	border: 1px solid black;
}

.productInfo__details {
	margin: 20px;
	align-self: flex-start;

	gap: 10px;
}

.productInfo__price {
	font-size: 1.3rem;
}

.productInfo__qtyControl {
	gap: 10px;
}

.productInfo__qtyBtns {
	border: 0;
	background: transparent;
	font-size: 1.7rem;
	font-weight: 500;
}

#productInfo__submitBtn {
	transition: all 100ms ease-in-out;
	font-weight: 700;
}

#productInfo__submitBtn:disabled {
	font-weight: 700;
	color: #7a7a7a;
	border: 2px solid rgb(109, 109, 109);
}

#productInfo__submitBtn:hover:not(:disabled) {
	background-color: black;
	color: white;
}

#qtySelectBar {
	width: 50px;
}

#cart__empty {
	gap: 10px;
}

.cart__img-small {
	aspect-ratio: 1/1;
	width: 100px;
	border: 1px solid red;
}

.cart__productView {
	padding-top: 30px;
	padding-bottom: 30px;
	padding-left: 2vw;
	padding-right: 0;
	justify-content: space-between;

	border-bottom: 2px solid #e2e2e2;

	gap: 15px;
}

.cart__productView:last-child {
	border-bottom: 0;
}

#cart_productView-left {
	gap: 20px;
}

#cart_productView-right {
	justify-content: space-between;
}

.cart__filledCart {
	padding-left: 10vw;
	padding-right: 10vw;
	gap: 20px;
}

#checkout-btn {
	width: fit-content;
	margin: 30px 0;
	margin-left: auto;
}

#cart__details {
	gap: 10px;
}

.cart__bottom {
}

#cart__orderTotal {
	justify-content: space-between;
	padding: 17px 0;
	border-top: 2px solid #e2e2e2;
	border-bottom: 2px solid #e2e2e2;
}

#cart__deleteBtn {
	transition: all 200ms ease-in-out;
}

#cart__deleteBtn:hover {
	color: red;
}
